<template>
  <h1>Check phản xạ của các bạn!</h1>
  <button @click="start" :disabled="isPlaying">CHƠI LUÔN</button>
  <Result v-if="resultMess" :result="result" :message="message">
  </Result>
  <div v-if="clickBox" class="box">
    <h3>Sẵn sàng ấn nào</h3>
  </div>
  <Block @click="clickMe" v-if="blockCond">
  </Block>
</template>

<script>
import Block from './components/Block.vue'
import Result from './components/Result.vue'



export default {
  name: 'App',
  components: {
    Block,
    Result,
  },
  data() {
    return {
      blockCond: false,
      isPlaying: false,
      delay: null,
      buttonDelay: 0,
      result: 0,
      message: "",
      resultMess: false,
      clickBox: false,
    }
  },
  methods: {
    start(e) {
      this.isPlaying = !this.isPlaying;
      this.delay = Math.floor(Math.random()*8000) + 3000;
      this.buttonDelay = e.timeStamp;
      this.clickBox = true;
      setTimeout(() => {
        this.blockCond = true;
        this.clickBox = false;
      }, this.delay);
      this.resultMess = false;
    },
    clickMe(e) {
      this.result = Math.floor((e.timeStamp - this.buttonDelay - this.delay)*100)/100;
      this.blockCond = false;
      if (this.result < 200) {
        this.message = "Nhanh vãi lòn! Are you S1mple?! Go PRO đi thôi chứ chờ gì nữa giáo sư!"
        } else if (this.result < 300) {
        this.message = "Bạn bắn được AWP rồi đấy!"
        } else if (this.result < 600) {
        this.message = "Bạn đã ngang trình anh em Kovacs Việt Nam!"
        } else {
        this.message = "Nói ngay! Mày là Quân hay Pam?"
        };
      this.isPlaying = false;
      this.resultMess = true;
      }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #444;
  margin-top: 60px;
}

button {
  margin: 50px;
  padding: 8px 15px;
  color: rgb(54, 216, 54);
  background-color: white;
  outline: none;
  border: 1px solid black;
  border-radius: 5px;
}


button:hover {
  color: white;
  background-color: rgb(54, 216, 54);
  border: 1px solid white;
}

.box {
  max-width: 100vw;
  width: 500px;
  height: 500px;
  border: 1px solid black;
  border-radius: 10px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

</style>
